<template>
    <div class="sales-box">
        <h3>————&nbsp;&nbsp; ◆ &nbsp;超实惠-促销直达 &nbsp;◆ &nbsp;&nbsp; ————</h3>
        <div class="sales-out">
            <div v-for="(item,i) in sname" :key="'item'+i" :class="'sales-div'+i">  
                <p class="p1">{{ item.title }}</p>
                <p class="p2">{{ item.viceTitle.substring(1,item.viceTitle.length-1) }}</p> 
                <div>
                    <img :src=" item.image ">
                </div>
            </div> 
        </div>
    </div>
</template> 

<script>
export default {
  props:{
    sname:{type:Array,default:[]}
  }
};
</script>

<style lang="less">
@import url("../less/less-var");
.sales {
  &-box {
    margin-top: 15px;
    height: 8.3rem;
    > h3 {
      text-align: center;
    }
    .sales-out {
      margin-top: 25px;
      > div {
        width: 33.33%;
        box-sizing: border-box;
        border: 1px solid #ccc;
        float: right;
        height: 250px;
        > .p1 {
          margin-top: 15px;
          font-size: 28px;
          font-weight: bold;
          text-align: center;
        }
        > .p2 {
          font-size: 25px;
          color: @pink;
          text-align: center;
        }
        > div {
          width: 150px;
          margin: auto;
          > img {
            width: 100%;
          }
        }
      }
      .sales-div0 {
        float: left;
        height: 300px;
        width: 50%;
        img {
          width: 130%;
        }
      }
      .sales-div1 {
        float: right;
        width: 50%;
        height: 150px;
        > .p1 {
          margin-top: 25px;
        }
        > p {
          text-align: left;
          margin-left: 15px;
        }
        img {
          width: 90%;
          position: relative;
          top: -85px;
          right: -120px;
        }
      }
      .sales-div2 {
        float: right;
        width: 50%;
        height: 150px;
        > .p1 {
          margin-top: 25px;
        }
        > p {
          text-align: left;
          margin-left: 15px;
        }
        img {
          width: 90%;
          position: relative;
          top: -85px;
          right: -120px;
        }
      }
    }
  }
}
</style>



