<template>
    <div class="right-bottom">
        <div class="bottom-nav-box">
            <div v-for="(item,i) in rightbottom" :key="'rightbottom'+i" class="bottomtitle-box">
                <p class="bottomtitle" 
                @click="sendFcid(item.sortKey,classify.rightfcid,i)" 
                :class="{'activeR':ind === i}">
                {{ item.title }}
                </p>
            </div>
            <div v-for="(item,i) in rightbottomimg" :key="'rightbottomimg'+i" class="bottomimg-box">
                <img :src="item.img" alt="">
                <p class="represent">{{ item.title }}</p>
                <div>
                    <p class="price">￥{{ item.price }}</p>
                    <p class="cfav">{{ item.cfav }} <i class="iconfont  icon-shoucang"></i></p> 
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { mapState, mapActions, mapMutations } from "vuex";

export default {
  data() {
    return {
      ind: 0
    };
  },
  props: {
    rightbottom: { type: Array, default: [] },
    rightbottomimg: { type: Array, default: [] }
  },
  computed: {
    ...mapState(["classify"])
  },
  methods: {
    ...mapActions(["loadClassifyRightBottomImg"]),
    sendFcid(sort, fcid,i) {
      // console.log(fcid);
      // console.log(sort);
      this.loadClassifyRightBottomImg({ sort, fcid });
      this.ind = i;
    },
  }
};
</script>

<style lang="less">
@import url("../less/less-var");
.right {
  &-bottom {
    margin-top: 55px;
    width: 100%;
    > .bottom-nav-box {
      display: flex;
      flex-wrap: wrap;
      > .bottomtitle-box {
        border-top: 1px solid #ccc;
        border-bottom: 1px solid #ccc;
        height: 90px;
        width: 33.33%;
        margin-bottom: 20px;
        .bottomtitle {
          margin-top: 25px;
          text-align: center;
          border-left: 1px solid #ccc;
          font-size: 30px;
          height: 40px;
        }
      }
      > .bottomimg-box {
        margin-bottom: 15px;
        width: 50%;
        > img {
          margin-left: 20px;
          width: 85%;
          height: 80%;
        }
        .represent {
          margin-left: 15px;
          font-size: 27px;
          height: 40px;
          line-height: 40px;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 1;
          overflow: hidden;
          -webkit-box-orient: vertical;
        }
        div {
          display: flex;
          justify-content: space-between;
          margin-top: 10px;
          .price {
            width: 50%;
            font-size: 28px;
            font-weight: bold;
            color: red;
            margin-left: 15px;
          }
          .cfav {
            color: #ccc;
            text-align: right;
            width: 50%;
            margin-right: 15px;
          }
        }
      }
    }
  }
}
.activeR {
  background-color: white;
   p {
    color: @pink;
  }
}
</style>


