<template>
    <div class="left-type">
        <div v-for="(item,i) in lefttitle" :key="'lefttitle'+i" class="left-in"  @click="changeBgc(i)" :class="{'active':ind === i}">
            <p @click="sendPid(item.maitKey,item.miniWallkey)">{{ item.title }}</p>
        </div>
    </div>   
</template>

<script>
import { mapState, mapActions, mapMutations } from "vuex";
export default {
  data() {
    return {
      ind: 0
    };
  },
  props: {
    lefttitle: { type: Array, default: [] }
  },
  computed: {
    ...mapState(["classify"])
  },
  methods: {
    ...mapActions(["loadClassifyRight","loadClassifyRightBottomImg"]),
    sendPid(pid,fcid) {
      this.loadClassifyRight(pid);
      this.loadClassifyRightBottomImg({fcid,sort:"pop"})
    },
    changeBgc: function(i) {
      this.ind = i;
    }
  },
  mounted() {
    // this.loadClassifyRightBottomImg();
  }
};
</script>

<style lang="less">
@import url("../less/less-var");
.left {
  &-type {
    height: 100%;
    background-color: @light;
    .left-in {
      height: 87px;
      > p {
        position: relative;
        bottom: -25px;
        border-left: 8px solid @light;
        height: 40px;
        font-size: 27px;
        text-align: center;
        line-height: 40px;
      }
    }
  }
}
.active {
    background-color: white;
    > p {
      font-weight: bold;
      color: @pink;
      border-left: 8px solid @pink !important;
    }
}
</style>


