<template>
    <div class="type-box">
      <div class="wrapper">
        <scroll-view class="a">
            <left-type :lefttitle="classify.lefttitle"></left-type>
        </scroll-view>
        <scroll-view class="b">
            <right-type :righttitle="classify.righttitle"></right-type>
        </scroll-view>
      </div>
    </div>
</template>

<script>
import LeftType from "../components/LeftType";
import RightType from "../components/RightType";
import { mapState, mapActions, mapMutations } from "vuex";

export default {
  components: {
    LeftType,
    RightType
  },
  computed: {
    ...mapState(["classify"])
  },
  methods: {
    ...mapActions(["loadClassifyPage"]),
    ...mapActions(["loadClassifyRight"])
  },
  mounted() {
    this.loadClassifyPage();
  }
};
</script>

<style lang="less">
@import url("../less/less-var");
.type {
  &-box {
    position: absolute;
    top: 100px-4; /*px*/
    bottom: @bottomHeight - 1; /*px*/
    left: 0px;
    right: 0px;
    >.wrapper {
      height: 100%;
      overflow: hidden;
      display: flex;
      justify-content: space-between;
      >.a {
        height: 100%;
        width: 25%;
      }
      >.b {
        height: 95%;
        width: 75%;
      }
    }
  }
}
</style>



