import api from "../../server/api";

const state = {
    ImportList: [],
    ImportP: [],
    ImportSortFilter: [],
    ImportDocs:[],
    Comments:[],
    RateScore:[]
}

const actions = {
    loadImportDatas({ commit }) {
        api.loadDataByimport((data) => {
            console.log(data)
            commit("setImport", data.data.hotWord.data);
            commit("setImportP", data.data.sketch.data.frontword)
        })
    },
    loadDataByParams({commit},params){
        commit("setImportDocs",[])
        api.loadDataByParams(params,(data)=>{
            commit("setImportSortFilter",data.result.sortFilter);
            commit("setImportDocs",data.result.wall.docs)
            console.log(data)
        })
    },
    loadeDataByComments({commit},tradeItemId){
        api.loadeDataByComment(tradeItemId,(data)=>{
            commit("setIComments",data.data.list);
            commit("setRateScore",data.data.rateScore)
        })
    }
}

const mutations = {
    setImport(state, data) {
        state.ImportList = data;
    },
    setImportP(state, data) {
        state.ImportP = data;
    },
    setImportSortFilter(state,data){
        state.ImportSortFilter = data;
    },
    setImportDocs(state,data){
        state.ImportDocs = data;
    },
    setIComments(state,data){
        state.Comments=data;
    },
    setRateScore(state,data){
        state.RateScore=data;
    },
}

export default {
    state,
    actions,
    mutations
}


