<template>
    <div class="home-box">
        <scroll-view class="wrapper">
            <swiper-component :swiperList="home.swiperList"></swiper-component>
            <choose-subject :cname="home.cname"></choose-subject>
            <sales :sname="home.sname"></sales>
            <hot :hname="home.hname"></hot>
            <guess-like :gname="home.gname"></guess-like>
        </scroll-view>
    </div>
</template>

<script>
import SwiperComponent from "../components/SwiperComponent";
import ChooseSubject from "../components/ChooseSubject";
import Sales from "../components/Sales";
import Hot from "../components/Hot";
import GuessLike from "../components/GuessLike";
import { mapState,mapActions,mapMutations } from 'vuex'

export default {
  components: {
    SwiperComponent,
    ChooseSubject,
    Sales,
    Hot,
    GuessLike
  },

  computed:{
    ...mapState(["home"])
  },
  methods:{
    ...mapActions(["loadHomeDatas"])
  },
  mounted() {
    this.loadHomeDatas();
  }
};
</script>


<style lang="less">
@import url("../less/less-var");
.home {
  &-box {
    .wrapper {
      position: absolute;
      top: @topHeight - 1; /*px*/
      bottom: @bottomHeight - 1; /*px*/
      left: 0px;
      right: 0px;
      overflow: hidden;
    }
  }
}
</style>



