<template>
    <div class="right-type">
        <div class="right-top" 
        v-for="(item,i) in righttitle" 
        :key="'righttitle'+i" @click="rightSearch(item.link,item.title)">
            <img :src="item.image" alt="">
            <p>{{ item.title }}</p>
        </div>
        <right-type-bottom :rightbottom="classify.rightbottom" :rightbottomimg="classify.rightbottomimg"></right-type-bottom>
    </div>   
</template>

<script>
import RightTypeBottom from "./RightTypeBottom";
import { mapState, mapActions, mapMutations } from "vuex";

export default {
  props: {
    righttitle: { type: Array, default: {} }
  },
  components: {
    RightTypeBottom
  },
  computed: {
    ...mapState(["classify"])
  },
  methods: {
    ...mapActions([
      "loadClassifyRight",
      "loadClassifyRightBottom",
      "loadClassifyRightBottomImg"
    ]),
    rightSearch(link, title) {
      var titles = link.indexOf("=")
      var titles1 = link.indexOf("&")
      var fcid = link.slice(titles+1,titles1)
      let params = {
        fcid,
        title,
        cKey: 16,
        page: 1,
        sort: "pop"
      }
      this.$router.push("/searchPage?params="+JSON.stringify(params));
    }
  },
  mounted() {
    this.loadClassifyRight(3627);
    this.loadClassifyRightBottom({ fcid: 3627, sort: "pop" });
    this.loadClassifyRightBottomImg({ fcid: 10062603, sort: "pop" });
  }
};
</script>

<style lang="less">
@import url("../less/less-var");
.right {
  &-type {
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    > .right-top {
      width: 33.33%;
      margin-top: 15px;
      > img {
        width: 75%;
        margin-left: 20px;
        margin-top: 18px;
      }
      p {
        text-align: center;
      }
    }
  }
}
</style>


