<template>
    <div ref="wrapper">
        <div class="content">
            <slot></slot>
        </div>
    </div>
</template>

<script>
import BScroll from "better-scroll";
export default {
    mounted(){
        setTimeout(() => {
            this.$nextTick(()=>{
                new BScroll(this.$refs.wrapper,{
                    click:true
                });
            });
        }, 500);
        
    }
}
</script>


