<template>
    <div class="choose-subject-box">
        <div v-for="(item,i) in cname" :key="'item'+i" class="subject-image">
            <img :src=" item.image ">
            <p>{{ item.title }}</p>
        </div>
    </div>
</template>

<script>
export default {
  props:{
    cname:{type:Array,default:[]}
  }
};
</script>

<style lang="less">
@import url("../less/less-var");
.choose{
    &-subject{
        &-box{
            .subject-image{
                width: 18%;
                display: inline-block;
                margin: 10px 7px;
                >img{
                    width: 95%;
                }
            }
            p{
                text-align: center;
            }
        }
    }
}

</style>



