import jsonpUtil from '../util/jsonpUtil'
import { isArray } from 'util';

const mce_multiget = "https://mce.mogucdn.com/jsonp/multiget/3";
const list_seach = "https://list.mogujie.com/search";
const mce_get = "https://mce.mogucdn.com/jsonp/get/3";
const inport_get = "https://list.mogujie.com/module/mget";
const rate_ratelist="https://rate.mogujie.com/h5/rate/jsonp/x6.rate.ratelist/v1";

const pidsParams = {
    appPlat: "m"
}

const importParams = {
    platform: "H5",
    code: "hotWord,sketch"
}


const error = function (error) {
    alert(error.message);
}

export default {
    //首页接口，分类页左侧接口
    loadDataByPids(pids, success = () => { }) {
        if (!pids) {
            throw new Error("请求参数错误");
        }
        if (!Array.isArray(pids)) {
            pidsParams.pids = pids;
        }
        if (Array.isArray(pids)) {
            let temp = "";
            for (const i in pids) {
                temp += pids[i] + ",";
            }
            pidsParams.pids = temp.replace(/,$/, "");
        }
        jsonpUtil.request(mce_multiget, pidsParams)
            .then((response) => {
                if (response.ok) {
                    return response.json();
                } else {
                    throw new Error("请求失败");
                }
            })
            .then(success)
            .catch(error)
    },

    //分类页右侧上部分接口
    loadDataByTypePid(pid, success = () => { }) {
        jsonpUtil.request(mce_get, { pid, pidsParams })
            .then((response) => {
                if (response.ok) {
                    return response.json();
                } else {
                    throw new Error("请求失败");
                }
            })
            .then(success)
            .catch(error)
    },

    //分类页右侧下半部分接口
    loadDataByTypeFcid({ fcid, sort }, success = () => { }) {
        jsonpUtil.request(list_seach, {
            fcid,
            page: 1,
            sort,
            cKey: "h5-cube"
        })
            .then((response) => {
                if (response.ok) {
                    return response.json();
                } else {
                    throw new Error("请求失败");
                }
            })
            .then(success)
            .catch(error)
    },

    //猜你喜欢接口
    loadDataByPid(params, success = () => { }) {
        jsonpUtil.request(list_seach, params)
            .then((response) => {
                if (response.ok) {
                    return response.json();
                } else {
                    throw new Error("请求失败");
                }
            })
            .then(success)
            .catch(error)
    },


    //首页到搜索页1关键字接口
    loadDataByimport(success = () => { }) {
        jsonpUtil.request(inport_get,importParams)
            .then((response) => {
                // console.log(response)
                if (response.ok) {
                    return response.json();
                } else {
                    throw new Error("请求失败");
                }
            })
            .then(success)
            .catch(error)
    },


    //搜索页点击搜索接口
    loadDataByParams(params,success = () =>{}){
        jsonpUtil.request(list_seach,params)
        .then((response)=>{
            if (response.ok) {
                return response.json();
            } else {
                throw new Error("请求失败");
            }
        })
        .then(success)
        .catch(error)
    },

    //详情页接口
    loadeDataByComment(tradeItemId,success = () =>{}){
        jsonpUtil.request(rate_ratelist,tradeItemId)
        .then((response)=>{
            if (response.ok) {
                return response.json();
            } else {
                throw new Error("请求失败");
            }
        })
        .then(success)
        .catch(error)
    },
}


