<template>
  <div class="search-box">
    <div class="search">
      <div class="sousuo"><img src="../assets/hui.png" alt=""></div>
			<input class="user-input" type="text"  :placeholder="JSON.parse($route.query.params).q"> 
			<i class="iconfont icon-gouwuche"></i>
    </div>
    <div class="title-box">
      <div v-for="(item,i) in topnav.ImportSortFilter" @click="changeBgc(i)" :key="'ImportSortFilter'+i" class="title-in" :class="{'activeS':ind === i}">
        <p @click="sendSortFilter($route.query.params,topnav.ImportSortFilter[i].sortKey)">{{ item.title }}</p>
      </div>  
    </div>
    <scroll-view class="swrapper">
      <div class="imgout-box">
        <div v-for="(item,i) in topnav.ImportDocs" :key="'ImportDocs'+i" class="img-box">
          <img :src="item.img">
          <p class="represent">{{ item.title }}</p>
          <div>
              <p class="price">￥{{ item.price }}</p>
              <p class="cfav">{{ item.cfav }} <i class="iconfont  icon-shoucang"></i></p> 
          </div>
        </div>
      </div>
    </scroll-view>
  </div>
</template>

<script>
import { mapState, mapActions, mapMutations } from "vuex";
import ScrollView from "../globalComponents/ScrollView";

export default {
  data() {
    return {
      ind: 0
    };
  },
  components: {
    ScrollView
  },
  computed: {
    ...mapState(["topnav"])
  },
  methods: {
    ...mapActions(["loadDataByParams"]),
    changeBgc: function(i) {
      this.ind = i;
    },
    sendSortFilter(params, sorts) {
      // console.log(params);
      // console.log(sorts);
      let obj = JSON.parse(params);
      for (var key in obj) {
        if (key === "sort") {
          obj[key] = sorts;
        }
      }
      this.loadDataByParams(obj);
    }
  },
  mounted() {
    let params = this.$route.query.params;
    this.loadDataByParams(JSON.parse(params));
  }
};
</script>

<style lang="less">
@import url("../less/less-var");
.search {
  &-box {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: white;
    z-index: 1;
    > .search {
      margin-top: 20px;
      > i {
        font-size: 45px; /*px*/
        text-align: center;
        margin-left: 120px;
      }
      > .sousuo {
        background-color: #eaeaea;
        position: fixed;
        right: 100px;
        top: 20px;
        display: block;
        height: 65px;
        width: 95px;
        img {
          width: 60%;
          margin-left: 20px;
          margin-top: 5px;
        }
      }
      > .user-input {
        margin-left: 30px;
        outline: none;
        width: 70%;
        height: 65px;
        text-indent: 0.7rem;
        line-height: 65px;
        border: 1px solid white;
        border-radius: 7px;
        background-color: #eaeaea;
        font-size: 30px;

        background: #eee url("../assets/sousuo.png") no-repeat 15px center;
      }
    }
    .title-box {
      margin-top: 20px;
      display: flex;
      flex-wrap: wrap;
      > .title-in {
        border-top: 1px solid #ccc;
        border-bottom: 1px solid #ccc;
        height: 90px;
        width: 33.33%;
        margin-bottom: 20px;
        text-align: center;
        > p {
          border-left: 1px solid #ccc;
          height: 40px;
          position: relative;
          top: 30px;
          line-height: 40px;
          font-size: 28px;
        }
      }
    }
    .swrapper {
      height: 85%;
      overflow: hidden;
      position: absolute;
      top: 220px;
      .imgout-box {
        height: 100%;
        display: flex;
        flex-wrap: wrap;
        > .img-box {
          width: 47%;
          margin-left: 15px;
          > img {
            width: 100%;
            height: 70%;
          }
          > .represent {
            margin-left: 15px;
            font-size: 25px;
            height: 65px;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            overflow: hidden;
            -webkit-box-orient: vertical;
          }
          div {
            display: flex;
            justify-content: space-between;
            margin-top: 20px;
            > .price {
              width: 50%;
              font-size: 28px;
              font-weight: bold;
              margin-left: 15px;
            }
            > .cfav {
              color: #ccc;
              text-align: right;
              width: 50%;
              margin-right: 15px;
            }
          }
        }
      }
    }
  }
}
.activeS {
  background-color: white;
  > p {
    color: @pink;
  }
}
</style>


