import Vue from 'vue'

import Vuex from 'vuex';

import home from './modules/home';
import classify from './modules/classify';
import topnav from './modules/topnav'

Vue.use(Vuex);

export default new Vuex.Store({
    modules:{
        home,
        classify,
        topnav
    }
})


