<template>
    <div class="hot-page">
        <h3>————&nbsp;&nbsp; ◆ &nbsp;热门分类 &nbsp;◆ &nbsp;&nbsp; ————</h3>
        <div v-for="(item,i) in hname" :key="'item'+i">
            <img :src=" item.image ">
            <p>{{ item.title }}</p>
        </div>
    </div>
</template>

<script>

export default {
  props:{
    hname:{type:Array,default:[]}
  }
};
</script>

<style lang="less">
@import url("../less/less-var");
.hot {
  &-page {
    height: 11.7rem;
    > h3 {
      text-align: center;
    }
    > div {
      width: 25%;
      float: left;
      img {
        width: 80%;
        margin-left: 15px;
        margin-top: 20px;
      }
      p {
        text-align: center;
      }
    }
  }
}
</style>



