import api from "../../server/api";

const state = {
    lefttitle:[],
    righttitle:[],
    rightbottom:[],
    rightbottomimg:[],
    rightfcid:[]
}

const actions = {
    loadClassifyPage({ commit }){
        api.loadDataByPids([117330], (data) => {
            // console.log(data)
            commit("setLeftTypes", data.data["117330"].list);
        });
    },
    loadClassifyRight({ commit },pid){  
        api.loadDataByTypePid(pid, (data) => {
            console.log(data)
            commit("setRightTypes", data.data.list);
        });
    },
    loadClassifyRightBottom({ commit },{fcid,sort}){
        api.loadDataByTypeFcid({fcid,sort}, (data) => {
            // console.log(data)
            commit("setRightBottomTypes", data.result.sortFilter);
        })
    },
    loadClassifyRightBottomImg({ commit },{fcid,sort}){
        commit("setRightBottomTypesImg", []);
        api.loadDataByTypeFcid({fcid,sort}, (data) => {
            // console.log(data)
            commit("setRightBottomTypesImg", data.result.wall.docs);
            commit("setRightFcid",data.result.param.fcid);
        })
    }
}

const mutations = {
    setLeftTypes(state,data){
        state.lefttitle=data;
    },
    setRightTypes(state,data){
        state.righttitle=data;
    },
    setRightBottomTypes(state,data){
        state.rightbottom=data;
    },
    setRightBottomTypesImg(state,data){
        state.rightbottomimg=data;
    },
    setRightFcid(state,data){
        state.rightfcid=data
    },
    
}

export default{
    state,
    actions,
    mutations
}


