<template>
    <div class="product_box">
       <scroll-view class="wrapper">
           <div class="product_img">
               <img :src="prouuctItems.img" alt="">
           </div>
           <div class="title_content">
               <span>{{prouuctItems.title}}</span>
           </div>
           <div class="price">
               <div class="price_now">
                   <span class="price_now_unit">￥</span>
                   <span class="price_now_price">{{ prouuctItems.price }}</span>
               </div>
               <div class="price_old">
                   <span class="price_old_price">￥{{ prouuctItems.orgPrice }}</span>
                   <div class="price_old_tags">
                       <span>品牌活动</span>
                   </div>
               </div>
           </div>
           <div class="service">
               <div class="service_cloumns">
                   <span class="service_cloumns--express">免邮费</span>
                   <span class="service_cloumns--address">江苏省南京市</span>
               </div>
               <div class="service_content">
                   <div class="service_content--item">
                       <span class="item_span1">
                           <img 
                           src="https://s10.mogucdn.com/mlcdn/c45406/180417_25kbfg1c3hdbd120394ji4b11bk2k_36x36.png_60x60.webp">
                       </span>
                       <span class="item_span2">72小时发货</span>
                   </div>
                   <div class="service_content--item">
                       <span class="item_span1">
                           <img 
                           src="https://s10.mogucdn.com/mlcdn/c45406/180417_25kbfg1c3hdbd120394ji4b11bk2k_36x36.png_60x60.webp">
                       </span>
                       <span class="item_span2">7天无理由退货</span>
                   </div>
                   <div class="service_content--item">
                       <span class="item_span1">
                           <img 
                           src="https://s10.mogucdn.com/mlcdn/c45406/180417_25kbfg1c3hdbd120394ji4b11bk2k_36x36.png_60x60.webp">
                       </span>
                       <span class="item_span2">退货补运费</span>
                   </div>
                   <span>
                       <!-- <img src="../../static/img/xiangzuo.png" alt=""> -->
                   </span>
               </div>
           </div>
           <div class="hrs"></div>
           <div class="more-sku">
               <span class="more-sku--text">请选择:颜色 尺码</span>
               <span class="more-sku--img">
                   <!-- <img src="../../static/img/xiangzuo.png" alt=""> -->
               </span>
           </div>
           <div class="hrs"></div>
           <div class="module-social">
               <div class="comment-head">
                   <div class="c-h--div">
                       <span class="comment-head--p">买家评价  1111</span>|
                       <span class="comment-head--x">销量 888</span>
                   </div>
                   <span>
                       <!-- <img src="../../static/img/xiangzuo.png" alt=""> -->
                   </span>
               </div>
               <div class="comment-tags">
                   <span class="tags-tag" v-for="(tag,i) in topnav.RateScore" :key="'tag'+i">{{ tag.key }}({{tag.value}})</span>
               </div>
               <div class="comment-list">
                   <div class="list-item" v-for="(item,i) in topnav.Comments" :key="'list'+i">
                       <div class="c-header">
                           <span class="c-img">
                               <img :src="item.user.avatar" alt="">
                           </span>
                           <span class="c-name">{{ item.user.uname }}</span>
                       </div>
                       <div class="c-content">
                           <span>{{ item.content }}</span>
                       </div>
                       <div class="c-time">
                           <span>{{ item.time }}</span>
                       </div>
                       <div class="c-atts">
                           <span>身高:</span>
                           <span>体重:</span>
                           <span>是否合身:</span>
                       </div>
                       <div class="c-images">
                           <img :src="item.user.avatar" alt="">
                       </div>
                   </div>
               </div>
           </div>
       </scroll-view>
    </div>
</template>
    
<script>
// import ScrollView from "../globalComponents/ScrollView";
import { mapState, mapActions } from "vuex";
export default {
  data() {
    return {
      prouuctItems: {}
    };
  },
  components: {
    // ScrollView
  },
  computed: {
    ...mapState(["topnav"])
  },
  methods: {
    ...mapActions(["loadeDataByComments"])
  },
  mounted: {},
  mounted() {
    //console.log(this.$route.query.items)
    this.prouuctItems = this.$route.query.items;
    this.loadeDataByComments({
      type: "1",
      itemId: this.$route.query.items.tradeItemId,
      appPlat: "m"
    });
  }
};
</script>
    
<style lang="less">
@import url("../less/less-var");
.product_box {
  width: 100%;
  // height:100%;
  background: white;
  position: absolute;
  top: 0px;
  right: 0px;
  left: 0px;
  bottom: 0px;
  z-index: 1;
  > .wrapper {
    position: absolute;
    top: 0px;
    right: 0px;
    left: 0px;
    bottom: 0px;
    // height: 100%;
    overflow: hidden;
    background: white;
    > .content {
      > .product_img {
        width: 100%;
        height: 800px;
        padding: 40px 0px 0px;
        display: flex;
        justify-content: space-around;
        > img {
          width: 80%;
          height: 100%;
          border-radius: 15px;
        }
      }
      > .title_content {
        padding: 30px 40px;
        > span {
          color: #424242;
          font-size: 32px;
          line-height: 40px;
        }
      }
      > .price {
        padding: 0px 40px;
        display: flex;
        flex-wrap: wrap;
        > .price_now {
          color: #333;
          display: flex;
          flex-wrap: wrap;
          > .price_now_unit {
            display: block;
            font-size: 28px;
            margin-top: 15px;
          }
          > .price_now_price {
            font-size: 70px;
          }
        }
        > .price_old {
          margin-left: 20px;
          > .price_old_price {
            margin-top: 10px;
            display: block;
            color: #999;
            text-decoration: line-through;
          }
          > .price_old_tags {
            margin-top: 5px;
            > span {
              font-size: 28px;
              background-color: rgb(255, 232, 238);
              color: rgb(255, 34, 85);
            }
          }
        }
      }
      > .service {
        padding-left: 40px;
        > .service_cloumns {
          display: flex;
          height: 80px;
          color: #999;
          padding-right: 40px;
          // border-bottom: @border #eee;
          > .service_cloumns--express,
          .service_cloumns--address {
            width: 33.33%;
            display: inline-block;
            line-height: 80px;
          }
        }
        > .service_content {
          margin-right: 40px;
          height: 80px;
          display: flex;
          flex-flow: nowrap;
          justify-content: space-around;
          > .service_content--item {
            display: flex;
            flex-flow: nowrap;
            line-height: 80px;
            > .item_span1 {
              display: block;
              width: 40px;
              height: 40px;
              // margin-left:20px;
              margin-right: 10px;
              > img {
                margin-top: 20px;
                width: 100%;
                height: 100%;
                margin-right: 10px;
              }
            }
            > .item_span2 {
              display: block;
              color: #666;
            }
          }
          > span {
            display: block;
            width: 40px;
            height: 40px;
            line-height: 80px;
            > img {
              margin-top: 20px;
              margin-left: 10px;
              width: 100%;
              height: 100%;
            }
          }
        }
      }
      > .hrs {
        height: 20px;
        background-color: rgb(234, 234, 234);
      }
      > .more-sku {
        display: flex;
        flex-flow: nowrap;
        justify-content: space-between;
        height: 80px;
        padding: 0px 40px;
        > .more-sku--text {
          display: block;
          line-height: 80px;
          color: #666;
        }
        > .more-sku--img {
          width: 35px;
          height: 35px;
          display: block;
          margin-top: 25px;
          > img {
            width: 100%;
            height: 100%;
          }
        }
      }
      > .module-social {
        padding: 0px 40px;
        > .comment-head {
          height: 80px;
          display: flex;
          flex-wrap: nowrap;
          justify-content: space-between;
          line-height: 80px;
          > .c-h--div {
            display: flex;
            flex-wrap: nowrap;
            color: #999;
            font-size: 25px;
            > .comment-head--p {
              display: block;
              margin-right: 10px;
            }
            > .comment-head--x {
              padding-left: 10px;
              display: block;
            }
          }
          > span {
            width: 30px;
            height: 30px;
            display: block;
            > img {
              width: 100%;
              height: 100%;
            }
          }
        }
        > .comment-tags {
          display: flex;
          flex-flow: row wrap;
          > .tags-tag {
            display: inline-block;
            height: 48px;
            line-height: 48px;
            color: #f25;
            background-color: #ffe8ee;
            margin-right: 10px;
            margin-top: 10px;
            font-size: 24px;
            border-radius: 4px;
            padding: 5px;
          }
        }
        > .comment-list {
          > .list-item {
            margin-top: 20px;
            margin-bottom: 35px;
            > .c-header {
              display: flex;
              > .c-img {
                display: block;
                width: 64px;
                height: 64px;
                > img {
                  width: 100%;
                  height: 100%;
                  border-radius: 50%;
                }
              }
              > .c-name {
                display: block;
                height: 64px;
                line-height: 64px;
                font-size: 30px;
                margin-left: 20px;
              }
            }
            > .c-content {
              margin-top: 20px;
              > span {
                font-size: 26px;
                color: #727272;
              }
            }
            > .c-time {
              margin-top: 10px;
              color: #999;
            }
            > .c-atts {
              display: flex;
              > span {
                display: block;
                margin-right: 25px;
                color: #999;
                font-size: 25px;
              }
            }
            > .c-images {
              margin-top: 15px;
              width: 150px;
              height: 150px;
              > img {
                width: 100%;
                height: 100%;
              }
            }
          }
        }
      }
    }
  }
}
</style>


