<template>
    <div class="swiper-box">
        <swiper :options="swiperOption">
            <swiper-slide v-for="(v,i) of swiperList" :key=" 'swiper'+i ">
                <img :src="v.image_800">
            </swiper-slide>
            <div class="swiper-pagination" slot="pagination"></div>
        </swiper>
    </div>
</template>

<script>
export default {
  data() {
    return {
      swiperOption: {
        autoplay:{
            disableOnInteraction:false,
            delay:500
        },
        pagination: {
          el: ".swiper-pagination"
        }
      }
    };
  },
  props: {
    swiperList: { type: Array, default: [] }
  }
};
</script>

<style lang="less">
@import url("../less/less-var");
.swiper {
  &-box {
    height: 300px;
    overflow-y: hidden;
    img {
      width: 100%;
    }
    .swiper-pagination-fraction,
    .swiper-pagination-custom,
    .swiper-container-horizontal > .swiper-pagination-bullets {
      bottom: 100px;
      left: -300px;
      width: 100%;
    }
    .swiper-pagination-bullet {
      width: 15px;
      height: 15px;
      display: inline-block;
      border-radius: 100%;
      background: white;
      opacity: 0.6;
    }
    .swiper-pagination-bullet-active {
      opacity: 1;
      background: #007aff;
    }
  }
}
</style>


