</template>

<script>
import TopNavBar from './components/TopNavBar'
import BottomNavBar from "./components/BottomNavBar";

export default {
  components:{
    TopNavBar,BottomNavBar
  }
}
</script>

<style lang="less">
@import url("./less/less-var");
 .actived,.exactActived{
   color:@pink;
 }
</style>




