<template>
	<div class="top-nav-box">
		<div class="search">
			<i v-show="!flag" class="iconfont icon-xiaoxizhongxin"></i>
			<i v-show="flag" class="iconfont icon-xiangzuojiantou" @click="flag=false"></i>
			<input class="user-input" type="text" @focus="flag=true"  v-model="userInput" :placeholder="topnav.ImportP" style=""> 
			<i v-show="!flag" class="iconfont icon-gouwuche"></i>
      <span v-show="flag" class="search" @click="sendQ(userInput)">搜索</span>
		</div>
		<transition enter-active-class="animated slideInRight" leave-active-class="animated slideInLeft">
			<div v-show="flag" class="search-content">
				<div class="search-history">
					<div class="history-left">
						<p> <i class="iconfont icon-sousuo"></i>搜索历史</p>
					</div>
					<div class="history-right">
						<i class="iconfont icon-lajixiang"></i>
					</div>
				</div>
				<div class="hot-save">
					<div>
						<p> <i class="iconfont icon-shoucang"></i>热门搜索</p>
						<div v-for="(item,i) in topnav.ImportList" 
            :key="'ImportList'+i" 
            class="import" 
            @click="sendP(item.query)">{{ item.query }}</div>
					</div>
				</div>
			</div>
		</transition>
	</div>
</template>

<script>
import { mapState, mapActions, mapMutations } from "vuex";

export default {
  data() {
    return {
      flag: false,
      userInput: ""
    };
  },
  computed: {
    ...mapState(["topnav"])
  },
  methods: {
    ...mapActions(["loadImportDatas"]),
    sendQ(q){
      if(q == ""){
        q = this.topnav.ImportP
      }
      let params = {
        q,
        cKey:46,
        page:1,
        sort:"pop"
      }
      this.$router.push("/searchPage?params="+JSON.stringify(params));
    },
    sendP(q){
      let hotsearch = {
        q,
        cKey:46,
        page:1,
        sort:"pop"
      }
      this.$router.push("/searchPage?params="+JSON.stringify(hotsearch));
    }
  },
  mounted() {
    this.loadImportDatas();
  }
};
</script>

<style lang="less">
@import url("../less/less-var");
.top-nav {
  &-box {
    border-bottom: @boder @dark; /*no*/
    .search {
      height: @topHeight; /*px*/
      background-color: @white;
      display: flex;
      flex-direction: row;
      flex-wrap: nowrap;
      justify-content: center;
      i {
        font-size: 50px; /*px*/
        line-height: @topHeight; /*px*/
        flex-grow: 1;
        text-align: center;
      }
      > .search {
        font-size: 30px; /*px*/
        line-height: @topHeight; /*px*/
        flex-grow: 1;
        text-align: center;
        color: black;
      }
      > .user-input {
        text-indent: 0.7rem;
        flex-grow: 5;
        border: 1px solid white;
        margin: 14px 0px; /*px*/
        border-radius: 7px;
        background-color: #eaeaea;
        font-size: 25px;
        background: #eee url("../assets/sousuo.png") no-repeat 15px center;
      }
      > .user-input:hover {
        outline: none;
        border: 1px solid #f9446b; /*no*/
        box-sizing: border-box;
      }
    }
    .search-content {
      z-index: 1;
      height: 100%;
      position: absolute;
      background-color: white;
      width: 100%;
      top: 85px;
      .search-history {
        height: 250px;
        display: flex;
        justify-content: space-between;
        border-top: 1px solid #ccc;
        border-bottom: 1px solid #ccc;
        margin-top: 10px;
        > .history-left {
          margin-top: 40px;
          margin-left: 50px;
          font-size: 30px;
          color: gray;
        }
        > .history-right {
          color: gray;
          margin-right: 50px;
          margin-top: 40px;
          i {
            font-size: 35px;
          }
        }
      }
      .hot-save {
        p {
          margin-top: 40px;
          margin-left: 50px;
          font-size: 30px;
          color: gray;
        }
        .import {
          border: 1px solid #ccc;
          width: 15%;
          float: left;
          margin-left: 30px;
          box-sizing: border-box;
          text-align: center;
          margin-top: 20px;
          height: 60px;
          line-height: 60px;
          border-radius: 7px;
        }
      }
    }
  }
}
</style>


