import api from "../../server/api";

const state = {
    swiperList: [],
    cname:[],
    sname: [],
    hname :[],
    gname: []
}

const actions = {
    loadHomeDatas({ commit }) {
        api.loadDataByPids([122224, 106930, 51833, 51836], (data) => {
            commit("setWarpList", data.data["122224"].list)
            commit("setTypes", data.data["106930"].list)
            commit("setBebefit", data.data["51833"].list)
            commit("setHotType", data.data["51836"].list)
        });
        api.loadDataByPid({
            pid:"9750",
            sort:"pop",
            cKey:46
        },(data)=>{
            commit("setLike",data.result.wall.docs)
        })
    }
}

const mutations = {
    setWarpList(state,data){
        state.swiperList=data;
    },
    setTypes(state,data){
        state.cname=data;
    },
    setBebefit(state,data){
        state.sname=data;
    },
    setHotType(state,data){
        state.hname=data;
    },
    setLike(state,data){
        state.gname=data;
    },
}

export default{
    state,
    actions,
    mutations
}


