<template>
    <div class="guess">
        <h3>————&nbsp;&nbsp; ◆ &nbsp;猜你喜欢 &nbsp;◆ &nbsp;&nbsp; ————</h3>
        <div v-for="(item,i) in gname" :key="'item'+i" class="guess-in" @click="senditem(item)">
            <img :src=" item.img " >
            <p class="represent">{{ item.title }}</p>
            <div>
                <p class="price">￥{{ item.price }}</p>
                <p class="cfav">{{ item.cfav }} <i class="iconfont  icon-shoucang"></i></p> 
            </div>
        </div>
    </div>
</template>

<script>

export default {
  props:{
    gname:{type:Array,default:{}}
  },
  methods:{
    senditem(items){
      console.log(items)
      this.$router.push({path:"/detailsPage",query:{items}})
    }
  },
  mounted(){
    // console.log(this.gname)
  }
};
</script>

<style lang="less">
@import url("../less/less-var");
.guess {
  //   margin-top: 11.5rem;
  height: 135rem;
  > h3 {
    text-align: center;
  }
  .guess-in {
    margin-top: 20px;
    height: 450px;
    width: 45%;
    float: left;
    padding: 15px;
    margin-left: 5px;
    img {
      width: 100%;
      height: 90%;
    }
    div {
      display: flex;
      justify-content: space-between;
    }
    .represent {
      font-size: 27px;
      height: 40px;
      line-height: 40px;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 1;
      overflow: hidden;
      -webkit-box-orient: vertical;
    }
    .price {
      width: 50%;
      font-size: 30px;
      font-weight: bold;
    }
    .cfav {
      color: #ccc;
      text-align: right;
      width: 50%;
      margin-top: 5px;
    }
  }
}
</style>



