<template>
    <div class="bottom-box">
        <router-link class="bottom-item" tag="div" to="/home">
            <i class="iconfont icon-shouye"></i>
            <span>首页</span>
        </router-link>
        <router-link class="bottom-item" tag="div" to="/classifyPage">
            <i class="iconfont icon-fenlei"></i>
            <span>分类</span>
        </router-link>
        <router-link class="bottom-item" tag="div" to="/cartPage">
            <i class="iconfont icon-gouwuche"></i>
            <span>购物车</span>
        </router-link>
        <router-link class="bottom-item" tag="div" to="/minePage">
            <i class="iconfont icon-tubiaolunkuo-"></i>
            <span>我的</span>
        </router-link>
    </div>
</template>

<script>
export default {
    methods: {
        changePage(data) {
            this.$emit("sendpage", {
                page: data
            })
        }
    }
}
</script>

<style lang="less">
@import url("../less/less-var");
.bottom{
    &-box{
        position: fixed;
        bottom: 0px;
        height: @bottomHeight;/*px*/
        border-top: @boder @dark;/*no*/
        display: inline-block;
        width: 100%;
        display:flex;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: center;
    }
    &-item{
        flex-grow: 1;
        text-align: center;
        margin: 6px 0px;/*px*/
        > i{
            display: block;
            font-size: @bottomHeight / 2;/*px*/
        }
        > span{
            font-size: @bottomHeight / 5 + 6;/*px*/
        }
    }
}
</style>


